import React from 'react';
import logo from '../../assets/img/logo.svg';
import Greetings from '../../containers/Greetings/Greetings';
import './Popup.css';

class Popup extends React.Component {

  getDuration() {
    chrome.tabs.query({active: true, currentWindow: true}, function(tabs) {
        chrome.tabs.sendMessage(tabs[0].id, {type: "getDuration"}, function(duration) {
          if(typeof count == "undefined") {
              // That's kind of bad
              if(chrome.runtime.lastError) {
                  // We couldn't talk to the content script, probably it's not there
                  console.log("Content script not found!!!");
              }
          } else {
              // Use duration
              return duration;
          }
        });
    });
  }

  constructor(props) {
    super(props);
    this.state = {
      duration: getDuration(),
    };
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/pages/Popup/Popup.js</code> and save to reload. Also, the duration for the video is {this.state.duration}.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React!
          </a>
        </header>
      </div>
    );
  }
}

export default Popup
